.cards2__card {
    box-shadow: 0px 0px 25px 0px rgba(142, 160, 173, 0.35);
    background: linear-gradient(90deg, #FBFDFF 0%, #D8EFFF 100.86%);

    padding: 30px;
    border-radius: 20px;

    width: 50%;
}

@media screen and (max-width: 700px) {
    .cards2__card {
        width: 100%;
    }
}